export default {
	
	data() {
		return {
			// 资讯列表
			message: []
		};
	},
	onLoad() {
		// 获取资讯列表
		this.getMessage();
	},
	methods: {
		// 获取资讯列表
		async getMessage() {
			const res = await uni.get('/api/getnewslist');
			console.log(res);
			if (res[1].data.status !== 0) {
				uni.baseToast('获取资讯列表失败');
			}
			this.message = res[1].data.message;
			console.log(this.message);
		},
		// 点击跳转到资讯详情页
		getNewsInfo(id) {
			console.log(id)
			uni.navigateTo({
				url:'/pages/newsInfo/newsInfo?id='+id
			});
		}
	}
};
